<template>
    <b-container class="py-5">
        <ve-pie :data="pieData" :settings="pieSettings"></ve-pie>
        <br/>
        <hr>
        <ve-line :data="lineData" :extend="extend"></ve-line>
    </b-container>
</template>

<script>
export default {
    name: 'AdminPayments',
    props: {
        initPaid: {
            type: Number
        },
        initUnpaid: {
            type: Number
        },
        initPaymentRow: {
            type: Array
        }
    },
    data() {
        this.extend = {
            series: {
                label: {
                    normal: {
                        show: true
                    }
                }
            }
        },
        this.pieSettings = {
            dimension: '',
            metrics: ''
        }
        return {
            lineData: {
                columns: ['', ''],
                rows: []
            },
            pieData: {
                columns: ['', ''],
                rows: [
                    { '': '', '': this.initUnpaid },
                    { '': '', '': this.initPaid }
                ]
            }
        }
    },
    created() {
        this.lineData.rows = this.initPaymentRow;
    }
}
</script>